.market-container {
  margin-top: 4px;
  .common-title {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 16px;
    color: #0f134f;
  }

  .market-summary-blocks {
    display: flex;
    justify-content: space-between;

    .market-summary-block {
      width: 49%;
      padding: 16px 0 7px;
      background: rgba(61, 86, 214, 0.1);
      border-radius: 8px;

      .block-title {
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        height: 24px;
        color: #0f134f;
        margin-bottom: 16px;
      }

      .block-money {
        font-family: 'Rubik-Medium';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 10px;
        color: #3d56d6;

        .block-percent {
          font-size: 14px;
          color: #3d56d6;
          margin-left: 4px;
        }
      }

      .block-process {
        > span {
          font-size: 14px;
          color: #5a5e89;
          font-weight: 500;

          &.process-text {
            color: #3d56d6;
          }
        }
      }

      &.lend-block {
        .block-process {
          .process-text {
            color: #1ed0ac;
          }
        }

        .block-percent {
          color: #1ed0ac;
        }
      }

      &:nth-child(2) {
        background: rgba(24, 193, 159, 0.1);
        .block-money {
          color: #18c19f;
        }
      }
    }

    .summary-footer {
      overflow: hidden;
      padding: 0 20px;
      margin-top: 15px;
      div {
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #545669;
        margin-bottom: 8px;
      }
      .num {
        font-family: 'Rubik-Medium';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #0f134f;
      }
      &.second {
        margin-top: 0;
      }
    }

    .market-summary-wrap {
      display: flex;
      padding: 0 20px;
      justify-content: space-between;
      border-bottom: 1px solid rgba(15, 19, 79, 0.05);
    }
  }

  .market-summary-list {
    margin-top: 20px;
    padding: 20px 20px 28px;
    border-radius: 8px;
    background-color: #fff;

    .ant-table-thead
      > tr
      > th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before {
      display: none;
    }
    .ant-table-column-title {
      font-size: 12px;
      > span {
        font-size: 12px;
      }
    }

    tr {
      cursor: pointer;
      &:hover {
        td {
          background-color: #f5f7fd !important;
          transition: all 0.2s ease-in;
        }
      }
      &:last-child {
        > td {
          border-bottom: none;
        }
      }
    }
    tbody td,
    thead th {
      text-align: right;
      &:first-child {
        width: 13%;
        text-align: left;
      }
      &:nth-child(2) {
        width: 17%;
      }
      &:nth-child(3) {
        width: 18%;
      }
      &:nth-child(4) {
        width: 19%;
      }
      &:nth-child(5) {
        width: 14%;
      }
      &:nth-child(6) {
        width: 19%;
      }
    }

    .table-alike {
      display: flex;

      > div:nth-child(1) {
        width: 13%;
      }
      > div:nth-child(2) {
        width: 17%;
      }
      > div:nth-child(3) {
        width: 18%;
      }
      > div:nth-child(4) {
        width: 19%;
      }
      > div:nth-child(5) {
        width: 14%;
      }
      > div:last-child {
        width: 19%;
        text-align: right;
      }

      &.table-header {
        margin-top: 16px;
        background: rgba(238, 241, 247, 0.5);

        > div {
          height: 32px;
          line-height: 32px;
          text-align: right;
          font-size: 12px;
          color: #84869e;

          &:first-child {
            text-align: left;
          }

          > span {
            font-size: 12px;
          }
        }
      }

      &.table-row {
        &:hover {
          transition: all ease-in 0.2s;
        }

        > div {
          height: 66px;
          line-height: 66px;
          text-align: right;
          color: #0f134f;
          font-size: 14px;

          &:first-child {
            text-align: left;
            position: relative;

            > img {
              width: 24px;
              height: 24px;
              position: absolute;
              top: 19px;
              left: 5px;
            }

            > p {
              text-indent: 30px;
              color: #0f134f;
              font-size: 14px;
              line-height: 16px;
              margin-top: 14px;
              margin-bottom: 4px;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;

              &:last-child {
                color: #84869e;
                font-size: 12px;
                line-height: 14px;
                margin: 0;
              }
            }
          }
        }
      }

      &.table-header > div,
      &.table-row > div {
        padding: 0 12px;
      }

      div.fw500 {
        span.fw500 {
          padding-left: 22px;
          min-width: 95px;
          display: inline-block;
          padding-right: 15px;
        }
        &.fire {
          span.fw500 {
            background: url(../images/mobile/fire.svg) left no-repeat;
            background-size: 14px 18px;
          }
        }
      }
    }
  }

  .market-detail-header {
    padding: 20px;
    height: 88px;
    margin-bottom: 20px;
    border-radius: 8px;
    background-color: #fff;

    .header-title {
      width: 20%;
      display: flex;
      align-items: center;
      margin: 0;

      > img {
        width: 40px;
        height: 40px;
        margin-right: 8px;
      }

      > span {
        font-size: 18px;
        font-weight: 500;
      }
    }

    .header-info {
      width: 80%;

      > div {
        width: 25%;
        text-align: right;

        > p {
          font-size: 16px;
          line-height: 20px;
          color: #0f134f;
          margin-bottom: 8px;

          &:nth-child(1) {
            line-height: 18px;

            > span {
              font-size: 12px;
              color: #84869e;
              font-weight: 400;
            }
          }
        }
      }
    }
  }

  .market-detail-info {
    width: 33%;
    padding: 0 0 14px 0;
    background: #f6f7fb;
    border-radius: 6px;

    .top {
      width: 100%;
      background: #fff;
      border-radius: 6px 6px 0 0;
      margin-bottom: 10px;

      .header-title {
        height: 60px;
        padding: 20px 20px 9px;
        border-bottom: 1px solid rgba(15, 19, 79, 0.05);
        position: relative;

        img {
          width: 30px;
          height: 30px;
          margin-right: 10px;
          vertical-align: top;
        }

        span {
          font-family: 'DM Sans';
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          color: #545669;
          line-height: 30px;

          strong {
            font-weight: 500;
            font-size: 18px;
            color: #0f134f;
            margin-right: 4px;
          }
        }

        .action {
          width: 20px;
          height: 20px;
          background: url(../images/mobile/category-icon.svg) 0 0 no-repeat;
          background-size: 100%;
          position: absolute;
          right: 20px;
          top: 24px;
          cursor: pointer;
          &:hover {
            background-image: url(../images/mobile/category-active-icon.svg);
          }
        }
      }

      section {
        width: 100%;
        padding: 11px 20px 10px;

        display: flex;

        .apy {
          width: 50%;
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          color: #545669;
        }

        span {
          display: block;
          height: 17px;
          line-height: 17px;
          margin-bottom: 6px;
        }
        strong {
          display: block;
          font-family: 'Rubik-Medium';
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          color: #0f134f;

          &.deposit {
            color: #3d56d6;
          }

          &.borrow {
            color: #18c19f;
          }
        }
      }
      .apy-box {
        width: calc(100% - 40px);
        margin: 0 auto;
        background: rgba(61, 86, 214, 0.06);
        border-radius: 6px;
        padding: 5px 15px;
        margin-bottom: 9px;
        div {
          margin-bottom: 10px;
          margin-top: 10px;
          display: flex;
          justify-content: space-between;

          span {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            color: #545669;

            &:nth-child(2) {
              font-family: 'Rubik';
              color: #3d56d6;
              white-space: nowrap;
              margin-left: 10px;
            }
          }
        }
      }
    }

    .tooltip-icon {
      margin-top: 0;
      margin-left: 4px;
      vertical-align: top;
    }

    .detail-title {
      display: flex;
      justify-content: space-between;
      line-height: 20px;
      color: #0f134f;
      font-weight: 600;
      margin-bottom: 16px;
      > a {
        font-size: 14px;
        font-weight: 400;
        color: #3d56d6;
      }
    }

    .detail-info {
      section {
        width: 100%;
        padding: 10px 20px 6px;
        display: flex;
      }
      .column {
        min-width: 50%;
        span {
          font-size: 12px;
        }
        > span {
          font-weight: 400;
          font-size: 12px;
          line-height: 17px;
          color: #545669;
          display: block;
          height: 17px;
          margin-bottom: 4px;

          &:nth-child(2) {
            font-family: 'Rubik-Medium';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            color: #0f134f;
          }
        }
      }
    }
  }

  .echarts-frame {
    position: relative;
    width: 65%;
    background: #fff;
    border-radius: 8px;
    padding: 20px;

    .left {
      position: absolute;
      top: 74px;
      left: 20px;
      color: #545669;
      font-size: 12px;
      display: block;
      z-index: 9;
    }

    .right {
      margin-left: 20px;
      margin-top: -15px;
      color: #84869e;
      font-size: 12px;
      display: block;
    }

    .echarts-title {
      line-height: 20px;
      color: #3d56d6;
      font-weight: 600;
      font-size: 14px;
      font-weight: 600;
      z-index: 1;
      width: calc(100% - 40px);
    }

    .echarts-subtitle {
      color: #84869e;
      font-size: 12px;
      font-weight: 400;
      margin-top: 5px;
    }

    .default-text {
      display: block;
      position: absolute;
      left: 20%;
      top: 0px;
      background: #ffffff;
      box-shadow: 0px 2px 20px rgba(135, 135, 135, 0.2);
      color: #333;
      font-size: 14px;
      display: inline-block;
      vertical-align: top;
      margin: 100px auto 20%;
      padding: 16px;
      color: #5a5e89;
      width: 206px;
      height: auto;
      &.include-mint .mint-p {
        display: flex;
      }

      &.hide {
        display: none;
      }

      p {
        margin-bottom: 0;
        line-height: 25px;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 17px;
        color: #84869e;
        display: flex;
        justify-content: space-between;
        margin-top: 6px;
        &:nth-child(3),
        &:nth-child(4) {
          text-indent: 6px;
        }

        &:nth-child(2),
        &:nth-child(5) {
          font-weight: 500;
          color: #5a5e89;
        }
        &.mint-p {
          display: none;
        }

        span {
          // font-family: Roboto;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 16px;
          text-align: right;
        }
        .sub-span {
          font-weight: 400;
          font-size: 12px;
          color: #0f134f;
          font-family: PingFang SC;
        }

        .green {
          color: #1ed0ac;
        }

        .blue {
          color: #3d56d6;
        }
      }
    }

    .gray-bar {
      padding: 12px;
      background: #f5f6f9;
      border-radius: 2px;
      margin-top: 20px;
      align-items: center;

      .title {
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        color: #0f134f;
      }

      .data-tip {
        font-family: PingFang SC;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 17px;
        color: #84869e;
      }

      .data {
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 18px;
        text-align: right;
        color: #3d56d6;
        padding-left: 18px;
        background: url(../images/mobile/fire.svg) 0 0 no-repeat;
        background-size: 12px 14px;
        margin-left: 8px;
      }
    }

    .bottom-bar {
      font-family: PingFang SC;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 17px;
      color: #84869e;
      margin-top: 9px;
      padding-left: 20px;
      background: url(../images/mobile/fire.svg) 0 0 no-repeat;
      background-size: 12px 14px;
    }

    .echarts-bar {
      background: rgba(61, 86, 214, 0.06);
      border-radius: 6px;
      padding: 20px;
      margin-top: 20px;
      margin-bottom: 10px;
      div {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #545669;

        strong {
          font-size: 12px;
          font-weight: 500;
          color: #0f134f;
        }
      }
      .data {
        font-family: 'Rubik-Medium';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 18px;
        text-align: right;
        color: #3d56d6;
        padding-left: 20px;
        background: url(../images/mobile/fire.svg) left -1px no-repeat;
        background-size: 14px 18px;
        margin-left: 8px;
      }
      .ant-checkbox + span {
        font-size: 12px;
        color: #545669;
      }
    }
  }

  .go-transcan > a,
  .market-footer > a {
    font-size: 14px;
    font-weight: 500;
    color: #3d56d6;
  }
}

.mltr {
  p {
    font-size: 12px;
    line-height: 20px;
    margin-bottom: 0 !important;
  }
}

.market-footer {
  background: #ffffff;
  box-shadow: 0px 10px 40px rgba(205, 205, 205, 0.1);
  border-radius: 8px;
  height: 146px;
  margin-top: 20px;

  .wallet-info {
    height: 61px;
    width: 100%;
    padding: 20px 20px 14px;
    border-bottom: 1px solid rgba(15, 19, 79, 0.05);

    .name {
      font-weight: 600;
      font-size: 14px;
      line-height: 26px;
      color: #0f134f;
    }

    button {
      width: 112px;
      height: 26px;
      background: rgba(61, 86, 214, 0.05);
      border: 1px solid #3d56d6;
      border-radius: 4px;
      font-weight: 400;
      font-size: 12px;
      line-height: 24px;
      text-align: center;
      color: #3d56d6;

      &:hover {
        background: #3d56d6;
        color: #fff;
      }

      &:nth-child(2) {
        background: rgba(24, 193, 159, 0.06);
        border: 1px solid #18c19f;
        border-radius: 4px;
        color: #18c19f;
        margin-left: 20px;
        &:hover {
          background: #18c19f;
          color: #fff;
        }
      }
    }
  }

  .wallet-detail {
    padding: 14px 20px 15px;
    span {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      height: 17px;
      margin: 6px 0 5px;
      color: #545669;

      &:nth-child(2) {
        font-weight: 500;
        font-size: 14px;
        color: #0f134f;
      }
      .font-rubik-medium {
        font-size: 14px;
        color: #0f134f;
      }
    }
  }

  .go-transcan {
    width: 33%;
  }

  .go-sun {
    margin-left: 2%;
  }
}

.market-container .market-summary-list a {
  &:last-child {
    .table-alike.table-row {
      border-bottom: none;
    }
  }
}
.show-all-markets {
  position: absolute;
  right: 12px;
  top: 12px;
  font-family: 'PingFang SC';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  color: #0f134f;
  > span {
    font-size: 12px;
  }
  &.show-all-markets-m {
    // position: static;
    display: inline-flex;
    padding-right: 5px;
    > span {
      // border-bottom: 1px dashed #545669;
      padding: 0;
      margin-left: 10px;
      > span {
        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #545669;
      }
    }
    &:hover {
      > span {
        color: #3d56d6 !important;
      }
    }
    .ant-checkbox {
      position: relative;
      top: 0px;
      border: none;
    }
    .ant-checkbox-inner {
      width: 12px;
      height: 12px;
      background: #d1d7e9;
      border-radius: 2px;
      border-bottom: none;
    }
    .ant-checkbox-checked .ant-checkbox-inner {
      background: #3d56d6;
    }
    &.ant-checkbox-wrapper-checked {
      > span {
        color: #3d56d6 !important;
      }
    }
    .ant-checkbox-inner::after {
      border-width: 1px;
      left: 23%;
    }
  }

  &.checkbox-detail-m {
    .ant-checkbox {
      top: 1px;
    }
  }
}

.use-rate {
  font-family: 'PingFang SC';
  font-style: normal;
  font-weight: 500 !important;
  font-size: 14px !important;
  line-height: 20px;
  color: #0f134f !important;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 12px;
  margin-bottom: 12px !important;
  border-bottom: 1px solid rgba(132, 134, 158, 0.4);
  > span {
    font-family: 'PingFang SC' !important;
  }
}

.fire {
  span.fw500 {
    padding-right: 20px;
    min-width: 95px;
    display: inline-block;
    background: url(../images/mobile/fire.svg) right no-repeat;
    background-size: 14px 18px;
    text-align: right;
    font-weight: 500;
  }
}

@media screen and (max-width: 1000px) {
  .market-container .market-summary-list {
    tbody td,
    thead th {
      width: auto;
    }
    .tokenDetail {
      display: none;
    }
    .table-alike > div:nth-child(1) {
      width: 5%;
    }
  }

  .right-container {
    width: calc(100% - 230px);

    &.mobile-right-container {
      width: 100%;
      .market-summary-list {
        .tooltip-icon {
          position: relative;
          top: -1px;
          & ~ span {
            position: relative;
            top: 2px;
          }
        }
      }
      .ant-table-column-sorter {
        position: relative;
        top: -2px;
      }
    }
  }

  .market-container .market-summary-blocks {
    display: block;

    .market-summary-block {
      width: 100%;

      &:first-child {
        margin-bottom: 20px;
      }
    }
  }

  .market-container {
    .market-info-block {
      display: block;
    }

    .market-detail-info,
    .echarts-frame {
      width: 100%;

      &:hover {
        .default-box {
          display: none;
        }
      }
    }

    .market-detail-info {
      margin-bottom: 20px;
    }
  }
}

@media screen and (max-width: 750px) {
  .table-row > div > p {
    display: none;
  }

  .market-container .market-summary-list .table-alike.table-row > div:first-child > img {
    left: 13px;
  }

  .table-frame {
    overflow-x: scroll;
  }

  .market-container .market-summary-list .table-alike {
    min-width: 750px;
    &.table-row > div,
    &.table-header > div {
      text-overflow: ellipsis;
      // overflow: hidden;
      white-space: nowrap;
    }
  }

  .market-container .market-detail-header .header-info > div > p {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .market-container .echarts-frame .left {
    left: -1%;
  }
}

@media screen and (max-width: 800px) {
  .market-container .market-detail-header {
    display: block;
    height: auto;
    overflow: hidden;

    .header-title {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }

    .header-info {
      display: block;
      width: 100%;

      > div {
        float: left;
        width: 50%;

        &:nth-child(2n + 1) {
          text-align: left;
        }

        &:nth-child(3) {
          clear: left;
        }
      }
    }
  }

  .market-container {
    .market-footer {
      justify-content: space-between;

      .go-transcan {
        width: auto;
        padding-top: 5px;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .mltr {
    &.ant-tooltip {
      // max-width: 100%;
      // .ant-tooltip-arrow {
      //   left: unset;
      //   right: 34px;
      // }
    }
  }

  .market-container .market-summary-list .table-alike.table-row > div,
  .market-container .market-summary-list .table-alike.table-header > div,
